body {
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.auth-form, .host-form, .pet-form, .address-form, .bank-form {
  max-width: 60vw;
  margin: 10px;
  padding: 10px;
  border: 1px solid #9C9C9C;
  border-radius: 5px;
}

.host-card {
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border: 1px solid #9C9C9C;
  border-radius: 5px;
}

.host-avatar {
  display: block;
}

.host-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.search-box {
  max-width: 85vw;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.hosts-page-container {
  max-width: 100vw;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.map-hosts-container {
  max-width: 100vw;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.map-container {
  width: 40vw;
  margin: 10px;
  padding: 10px;
}

.hosts-container {
  width: 60vw;
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.account-container {
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pets-container {
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}